.blog {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-color: #eee;
  &__empty {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.8rem;
    & > div {
      display: flex;
    }
    &__link {
      margin: 0  0 0 8px;
      color: $main-bg-color;
    }
  }
  &__posts {
    width: 70%;
    height: 100%;
  }
}

@media (max-width: 768px) {
  .blog__posts {
    width: 100%;
  }
}
