.gallery {
    display: flex;
    flex-direction: column;
    background: white;
    padding: 150px 20px;
    height: 100%;
    &__contents {
        display: flex;
        flex-wrap: wrap;
        margin-top: 20px;
    }
    &__empty {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}